性疱疹症狀者不可施打。<b>*一般正常現象*</b></li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>+ 注射後24小時形狀固定，期間避免重壓及接觸注射區域。玻尿酸術後可進行冰敷，減輕紅腫狀況（紅腫狀況依個人體質不同），但不要在注射部位熱敷。</li> <li>+ 玻尿酸注射治療後，可能會有下列暫時性的現象產生，如瘀血、發紅、疼痛、搔癢、微腫及注射部位摸到有彈性的物質存在，這些症狀都是正常的，於療程後幾天至1～2週後消失。</li> <li>+ 術後一切基礎保養正常，不要特別在區部位按摩。可進行施打部位的濕敷或者加強保濕產品護理。</li> <li>+ 一周內勿游泳∕三溫暖∕烤箱∕熱瑜珈，洗臉時務必輕柔，避免擠壓。</li> <li>+ 注射在鼻樑部位時，一周內儘量避免揉鼻子、戴眼鏡。</li> <li>+ 玻尿酸治療效果會因人而異，醫師會視個人需要，建議是否重覆注射。</li> <li>+ 療程後1～2週內回診追蹤，可視個人情況與需求追加注射。</li> <li>+ 如有用阿斯匹靈或其他類似藥物，請注意這會增加青腫及流血。</li></ul></div>")],2)}
